import React, { useEffect } from "react";
import { MapContainer, TileLayer, ZoomControl } from "react-leaflet";

import "leaflet-fullscreen/dist/Leaflet.fullscreen.js";
import "leaflet/dist/leaflet.css";
import "leaflet-fullscreen/dist/leaflet.fullscreen.css";

const position = [0, 0];
const oflineURL = "./map/{z}/{x}/{y}.png";

function Map() {
  useEffect(() => {
    //positioning of controller
    document
      .querySelector(".leaflet-control-container")
      .firstChild.classList.replace("leaflet-left", "leaflet-right");

    document.querySelector(".leaflet-control-container").firstChild.style.top =
      "8%";

    document
      .querySelector(".leaflet-control-attribution")
      .children[0].setAttribute("href", "https://fazlulkarim.netlify.app/");
    document
      .querySelector(".leaflet-control-attribution")
      .children[0].setAttribute("target", "_blank");
    document.querySelector(
      ".leaflet-control-attribution"
    ).children[0].innerText = "Develop By Md. Fazlul Karim";
  }, []);

  return (
    <div>
      <MapContainer
        center={position}
        minZoom={2}
        zoom={2}
        maxZoom={5}
        zoomControl={false}
        scrollWheelZoom={true}
        fullscreenControl={true}
        style={{
          width: "100vw",
          height: "100vh",
          margin: "0 auto",
        }}
      >
        <TileLayer url={oflineURL} noWrap="true" continuosWorld="false" />
        <ZoomControl position="topright" />
      </MapContainer>
    </div>
  );
}

export default Map;
