import React, { useState, useRef } from "react";
import { FaArrowRight, FaArrowLeft } from "react-icons/fa";
import { FiMapPin } from "react-icons/fi";
import "./sidebar.css";
function Sidbar() {
  const [collaps, setCollaps] = useState(true);

  const reference = useRef();

  const collapsSidebar = () => {
    if (collaps) {
      reference.current.style.transition = "all .5s";
      reference.current.style.left = "-16rem";
    } else {
      reference.current.style.transition = "all .5s";
      reference.current.style.left = "0";
    }
    setCollaps(!collaps);
  };

  return (
    <div className="sidebar" ref={reference}>
      <h1 className="title">pubg mobile map</h1>
      <div className="tools">
        <span>Tools:</span>
        <div className="tools__container">
          <div className="icon">
            <FiMapPin></FiMapPin> <p className="icon__text">Marker</p>
          </div>
          <div className="icon"></div>
          <div className="icon"></div>
          <div className="icon"></div>
        </div>
        <span> More Tools are Comming Soon</span>
      </div>
      <div className="siebar__controller" onClick={collapsSidebar}>
        {collaps ? (
          <FaArrowRight className="icons" />
        ) : (
          <FaArrowLeft className="icons" />
        )}
      </div>
    </div>
  );
}

export default Sidbar;
